.events {
    display: flex;
    position:relative;
    flex-direction: column;
    align-content: center;
    padding: 20px;
    overflow: hidden;
    line-height: 1;
    gap:15px;
}

.event-card {
    width: 100%;
    height: calc((100vw)*0.44);
    border-radius: 8px; /* 이미지 둥근 모서리 */
}

.event-card-image {
    width: 100%;
    height: 100%;
    border-radius: 10px; /* 이미지 둥근 모서리 */
}